<template>
  <div class="notifier" id="notifier" v-show="notifier.show">
    <div class="notifier-wrapper">
      <div class="notifier-item" :class="notifier.type">
        <div class="notifier-label">{{notifier.msg}}</div>
        <div class="notifier-close" @click="onClose">
          <font-awesome-icon icon="times"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    notifier () {
      return this.$store.state.notifier
    }
  },
  methods: {
    onClose () {
      this.$store.commit('toggleNotifier', {
        show: false,
        type: 'info',
        msg: ''
      })
    }
  }
}
</script>

